/*----------------------------------------------------
header
------------------------------------------------------*/
#header .fa-twitter {
  color: #00acee;
}

#header .fa-instagram {
  color: #daa1e6;
}

#header .fab {
  display: inline-block;
  font-size: 1.15rem;
  padding-right: 5px;
}

/*Video*/
.bg-video {
  width: 100%;
  height: 100vh;
  -o-object-fit: cover;
     object-fit: cover;
}

/*menu-btn*/
.menu-btn {
  display: block;
  position: fixed;
  top: 25px;
  right: 45px;
  z-index: 999;
  width: 40px;
  height: 40px;
  border-radius: 10px;
  cursor: pointer;
  transition: all 0.6s;
}

.menu-btn span {
  position: absolute;
  left: 5px;
  display: block;
  width: 30px;
  height: 2px;
  border-radius: 1px;
  background-color: #fff;
  transition: all 0.6s;
}

.menu-btn span:nth-child(1) {
  top: 9px;
}

.menu-btn span:nth-child(2) {
  top: 19px;
}

.menu-btn span:nth-child(3) {
  top: 29px;
}

.open .menu-btn {
  background-color: rgba(250, 250, 250, 0);
}

.open .menu-btn span {
  background-color: #fff;
}

.open .menu-btn span:nth-child(1) {
  transform: translateY(10px) rotate(-315deg);
}

.open .menu-btn span:nth-child(2) {
  opacity: 0;
}

.open .menu-btn span:nth-child(3) {
  transform: translateY(-10px) rotate(315deg);
}

/*----------------------------------------------------
nav
------------------------------------------------------*/
#nav {
  width: 300px;
  min-height: 100vh;
  color: #ffffff;
  z-index: 99;
  position: fixed;
  top: 0;
  left: -400px;
  opacity: 0;
  top: 0;
  transition: all 0.6s;
  overflow-x: hidden;
  overflow-y: auto;
  font-family: "Quicksand", sans-serif;
}

#nav li {
  padding: 0 0;
  border-bottom: 1px solid #fff;
}

#nav li:first-child {
  border-top: 1px solid #fff;
}

#nav ul {
  padding: 70px 0;
}

#nav a {
  padding: 10px;
  color: #fff;
  text-align: center;
  display: block;
  transition: all 0.6s;
}

#nav a:hover {
  color: #1a1a1a;
  background-color: #fff;
  font-size: 1.3rem;
  transition: all 0.6s;
}

#nav a:hover + .fab {
  font-size: 2rem;
}

.open #nav {
  opacity: 1;
  left: 0;
}

/*mask*/
#mask {
  display: none;
  transition: all 0.6s;
}

.open #mask {
  display: block;
  position: fixed;
  left: 0;
  top: 0;
  z-index: 10;
  background-color: rgba(20, 20, 20, 0.7);
  width: 100%;
  height: 100%;
  min-height: 100vh;
}

/*----------------------------------------------------
mainコンテンツ部分は
------------------------------------------------------*/
